import * as React from 'react';
import './Portal.css';
import { Logo } from '../Common/Logo';
import { HeaderMenu } from '../HeaderMenu/HeaderMenu';
import { CategorySideBar } from '../CategorySideBar/CategorySideBar';
import { Content } from '../Content/Content';
import { TagsSideBar } from '../TagsSideBar/TagsSideBar';
import { LoadingSpinner, DAL } from 'artbase-common';
import { IMenuInfo } from '../HeaderMenu/MenuItem';

export interface IPortalState { 
    bookmarks: any;
    categories: any;
    bookmarksLoaded: boolean;
}

export interface IPortalProps { }

export class Portal extends React.Component<IPortalProps, IPortalState> {

    constructor(props: IPortalProps) {
        super(props);
        this.state = {
            bookmarks: [],
            categories: [],
            bookmarksLoaded: true,
        };
    }

    componentDidMount() {
        DAL.getLastBookmarks(8).then((res: any) => {
            this.setState({
                bookmarks: res.data,
                bookmarksLoaded: true
            });
        });

        DAL.getCategories().then((res: any) => {
            this.setState({
                categories: res.data
            })
        })
    }

    private menuItems: IMenuInfo[] = [
        {
            name: "All",
            icon: "img/2.png",
            onClick: this.getAllBookmarks
        },
        {
            name: "My Links",
            icon: "img/6.png",
            onClick: this.getMyLinks
        },
        {
            name: "My Claps",
            icon: "img/4.png",
            onClick: this.getMyClaps
        }
    ]

    private getByCategory() {

    }

    private getByMainOption() {

    }

    private getByTags() {

    }

    private getAllBookmarks() {

    }

    private getMyLinks() {

    }

    private getMyClaps() {

    }

    render() {
        if (this.state.bookmarksLoaded) {
            return (
                <div className='artB-Portal'>
                    <Logo />
                    <HeaderMenu menuItems={this.menuItems} />
                    <CategorySideBar onSelectedValueChanged={this.getByCategory} options={this.state.categories} />
                    <Content bookmarks={this.state.bookmarks} />
                    <TagsSideBar />
                </div>
            );
        }
        return <LoadingSpinner />
    }
}