import * as React from 'react';
import './CategorySideBar.css';

export interface IOption {
    id: number;
    name: string;
}

export interface ICategorySideBarState { 
    value: string;
}

export interface ICategorySideBarProps {
    onSelectedValueChanged(option: IOption): void;
    options: IOption[];
}

export class CategorySideBar extends React.Component<ICategorySideBarProps, ICategorySideBarState> {

    constructor(props: ICategorySideBarProps) {
        super(props);
        this.state = { value: '' };
    }

    private handleChange = (event: any) => {
        this.props.onSelectedValueChanged(this.props.options.find((option: IOption) => option.id == event.target.value));
        this.setState ({
            value: event.target.value
        });
    }

    render() {
        return (
            <div className='artB-CategorySideBar'>
                <span className='artB-Title'>Category: </span>
                <ul>
                    {
                        this.props.options.map((option) => (
                          <li><a href="#" key={option.id} onClick={this.handleChange}>{option.name}</a></li>  
                        ))
                    }
                    <li><a href="#">UI/UX</a></li>
                    <li><a href="#">CSS</a></li>
                    <li><a href="#">SharePoint</a></li>
                    <li><a href="#">BA</a></li>
                </ul>
            </div>
        );
    }
}