import * as React from 'react';
import * as classnames from 'classnames';
import './HeaderMenu.css';

export interface IMenuInfo {
    name: string;
    icon: string;
    onClick(): void;
}

export interface IMenuItemProps {
    menuInfo: IMenuInfo;
    showAsActive: boolean;
 }

export class MenuItem extends React.Component<IMenuItemProps> {
    public render() {
        const classNames = classnames('artB-MenuItem', {
            'artB-MenuItem-active': this.props.showAsActive
        });

        const classNamesIcon = classnames('artB-Icon', {
            'artB-Icon-active': this.props.showAsActive
        });

        return (
            <div onClick={this.props.menuInfo.onClick} className={classNames}>
                <img className={classNamesIcon} src={this.props.menuInfo.icon} alt="" height="22" width="22"/>
                {this.props.menuInfo.name}
            </div>
        );
    }
}