import * as React from 'react';
import './Content.css';

export interface IBookmarkState { }

export interface IBookmarkProps { 
    name: string;
}

export class Bookmark extends React.Component<IBookmarkProps, IBookmarkState> {

    constructor(props: IBookmarkProps) {
        super(props);
    }

    render() {
        return (
            <div className='artB-Bookmark'>
                <img className="artB-Image" src="data:image/jpeg;base64," 
                alt=""/>
                <span className='artB-Name'> {this.props.name} </span>
                
            </div>
        );
    }
}