import * as React from 'react';
import './HeaderMenu.css';
import { MenuItem, IMenuInfo } from './MenuItem';

export interface IHeaderMenuState {
    activeMenuItem: IMenuInfo;
 }

export interface IHeaderMenuProps { 
    menuItems: IMenuInfo[];
}

export class HeaderMenu extends React.Component<IHeaderMenuProps, IHeaderMenuState> {

    constructor(props: IHeaderMenuProps) {
        super(props);

        this.state = {
            activeMenuItem: props.menuItems.length ? props.menuItems[0] : null
        }
    }

    private setActiveMenuItem(menuItem: IMenuInfo) {
        this.setState({ activeMenuItem: menuItem });
    }

    render() {
        return (
            <div className='artB-HeaderMenu'>
                {
                    this.props.menuItems.map((menuItem, index) => (
                        <div key={index} className='artB-menuItemContainer' onClick={() => this.setActiveMenuItem(menuItem)}>
                            <MenuItem key={index} menuInfo={menuItem} showAsActive={menuItem === this.state.activeMenuItem } />
                        </div>
                    ))
                }
            </div>
        );
    }
}